import { useQuery } from '@tanstack/react-query';
import Role from '../../types/Role';
import axios from 'axios';
import { SERVICE_COMPANY_URL } from '@/app/utility/constans';
import { useTranslation } from 'react-i18next';

type SortDirection = 'asc' | 'desc' | undefined;

const fetchRoles = async (
    token: string,
    pageSize: number,
    page: number,
    sortBy: string,
    sortDirection: SortDirection,
    phrase: string
): Promise<Role[]> => {

    try {
        const response = await axios.get(`${SERVICE_COMPANY_URL}/api/roles`, {
            headers: {
                Authorization: `Bearer ${token}`,
            },
            params: {
                pageSize,
                page,
                sortBy,
                sortDirection,
                phrase
            },
        });

        return response.data.data;

    } catch (error) {
        if (axios.isAxiosError(error) && error.response?.status === 401) {
            window.location.href = '/user/logout';
        }

        throw error;
    }
};

const useRolesQuery = (pageSize: number, page: number, sortBy: string, sortDirection: SortDirection, phrase: string) => {
    const { t } = useTranslation();

    console.log('useRolesQuery ');

    return useQuery<object>({
        queryKey: ['roles', pageSize, page, sortBy, sortDirection, phrase],
        queryFn: async () => {
            const token = localStorage.getItem('token');

            if (!token) {
                throw new Error(t('common.message.tokenIsMissing'));
            }

            console.log('useRolesQuery 2');

            const roles = await fetchRoles(token, pageSize, page, sortBy, sortDirection, phrase);

            return roles || [];
        }
    });
};

export default useRolesQuery;